import { useCallback, useState } from 'react';
import { useParams } from 'react-router';
import Product from '../components/Product';
import products from '../components/products.json';
import { useFilter, useProducts } from '../misc/custom-hooks';

function getFilteredData(filters, ProductsData) {
  if (filters.delivery) {
    return ProductsData.filter(product => product.delivery === true);
  } else if (filters.inStock) {
    return ProductsData.filter(product => product.inStock === true);
  } else if (filters.expensive) {
    return ProductsData.filter(product => product.price > 100);
  } else {
    return ProductsData;
  }
}
const Category = () => {
  const { id } = useParams();
  const CategorisedData = products.filter(product => product.categoryId === id);
  const [data] = useState(CategorisedData);

  const [filters, filtersDispatch] = useFilter({
    delivery: false,
    inStock: false,
    expensive: false,
  });
  const [state, dispatch] = useProducts();

  const handleChange = useCallback(
    ev => {
      const { name, checked } = ev.target;
      filtersDispatch({
        type: 'add',
        payload: {
          Name: name,
          isChecked: checked,
        },
      });
    },
    [filtersDispatch]
  );
  const filteredData = getFilteredData(filters, data);

  const onCartClick = useCallback((id, price) => {
    dispatch({ type: 'ADD', payload: { ProductId: id, ProductPrice: price } });
  }, []);
  const renderProducts = Data => {
    return Data.map(result => {
      const currentProduct = state.find(
        cartProduct => cartProduct.id === result.id
      );

      return (
        <Product
          key={result.id}
          result={result}
          onCartClick={onCartClick}
          State={currentProduct}
        />
      );
    });
  };
  return (
    <>
      <div className="sidenav">
        <h3>Filter</h3>

        <div className="checkbox-container">
          <input
            className="checkbox"
            type="checkbox"
            name="delivery"
            id="delivery"
            onChange={e => handleChange(e)}
            checked={filters.delivery}
          />
          <label htmlFor="delivery">Delivery</label>
        </div>
        <div className="checkbox-container">
          <input
            className="checkbox"
            type="checkbox"
            name="inStock"
            id="inStock"
            onChange={e => handleChange(e)}
            checked={filters.inStock}
          />
          <label htmlFor="inStock">InStock</label>
        </div>
        <div className="checkbox-container">
          <input
            className="checkbox"
            type="checkbox"
            name="expensive"
            id="expensive"
            onChange={e => handleChange(e)}
            checked={filters.expensive}
          />
          <label htmlFor="expensive">Expensive</label>
        </div>
      </div>
      <div className="CategoryItem-container">
        {filteredData.length > 0 ? (
          renderProducts(filteredData)
        ) : (
          <div className="Alert">No items Available</div>
        )}
      </div>
    </>
  );
};

export default Category;
